import React, { useState } from "react";

const PromptItem = ({ prompt, onAddMoreClick, onEdit, onRightClick }) => {
  const [isEditing, setIsEditing] = useState(false);
  const [editValue, setEditValue] = useState(prompt.value);

  const keywords = ["photo", "photography", "image", "picture"];
  const containsKeyword = keywords.some((keyword) =>
    prompt.value.toLowerCase().includes(keyword)
  );

  const handleAddMoreClick = (event) => {
    event.stopPropagation();
    if (onAddMoreClick) {
      onAddMoreClick(prompt.id);
    }
  };

  const handleDoubleClick = () => {
    setIsEditing(true);
  };

  const handleEditChange = (event) => {
    setEditValue(event.target.value);
  };

  const handleEditSubmit = () => {
    if (onEdit) {
      onEdit(prompt.id, editValue);
    }
    setIsEditing(false);
  };

  const handleKeyPress = (event) => {
    if (event.key === "Enter") {
      event.preventDefault();
      handleEditSubmit();
    }
  };

  const handleRightClick = (event) => {
    event.preventDefault();
    if (onRightClick) {
      onRightClick(event, prompt.id);
    }
  };

  const liClassName = isEditing ? "promptItem editing" : "promptItem";

  return (
    <li
      className={liClassName}
      style={{ color: containsKeyword ? "var(--main-color)" : "inherit" }}
      onDoubleClick={handleDoubleClick}
      onContextMenu={handleRightClick} // Attach the right-click handler to the <li> element
    >
      {isEditing ? (
        <input
          type="text"
          value={editValue}
          onChange={handleEditChange}
          onBlur={handleEditSubmit}
          onKeyPress={handleKeyPress}
          autoFocus
          className="editingPromptItem"
        />
      ) : (
        <>
          {prompt.value}
          {containsKeyword && (
            <span onClick={handleAddMoreClick}>Add more</span>
          )}
        </>
      )}
    </li>
  );
};

export default PromptItem;
