import AmbientLight from "./AmbientLight";
import LightTime from "./LightTime";

const LightingOptions = ({ onBack }) => {
  return (
    <div>
      <p className="options-title">Lighting Options</p>
      <div className="lightingOptionsContainer">
        <AmbientLight />
        <LightTime />
      </div>
      <button onClick={onBack} className="back-btn">
        Back
      </button>
    </div>
  );
};

export default LightingOptions;
