import React, { useState } from "react";

const LightTime = () => {
  const [sliderValue, setSliderValue] = useState(1);
  const options = [
    {
      title: "Morning",
      value: "Morning light",
      imgSrc: require("../icons/morning.svg").default,
    },
    {
      title: "Noon",
      value: "sunny noon light",
      imgSrc: require("../icons/noon.svg").default,
    },
    {
      title: "Night",
      value: "Night time light",
      imgSrc: require("../icons/night.svg").default,
    },
  ];

  const handleChange = (event) => {
    setSliderValue(parseInt(event.target.value, 10));
  };

  return (
    <div className="lightTime">
      <div className="icons">
        {options.map((option, index) => (
          <img
            key={index}
            src={option.imgSrc}
            alt={option.title}
            className={`icon ${sliderValue === option.value ? "active" : ""}`}
          />
        ))}
      </div>
      <input
        type="range"
        min="1"
        max="3"
        value={sliderValue}
        onChange={handleChange}
        step="1"
        className="slider"
      />
      <div className="titles">
        {options.map((option, index) => (
          <div
            key={index}
            className={`title ${sliderValue === option.value ? "active" : ""}`}
          >
            {option.title}
          </div>
        ))}
      </div>
    </div>
  );
};

export default LightTime;
