import React, { useState, useRef } from "react";
import { v4 as uuidv4 } from "uuid";

const PromptInput = ({ onSubmission }) => {
  const [submittedValue, setSubmittedValue] = useState("");
  const textareaRef = useRef(null); // Create a ref for the textarea

  const handleInputChange = (event) => {
    setSubmittedValue(event.target.value);
  };

  const handleSubmit = () => {
    if (submittedValue.trim() === "") {
      return;
    }

    const id = uuidv4();
    const promptData = {
      id,
      promptValue: submittedValue,
      creationDate: new Date().toISOString(),
    };

    localStorage.setItem(`promptInput-${id}`, JSON.stringify(promptData));
    updatePromptOrder(id);

    setSubmittedValue("");
    textareaRef.current.focus();

    if (onSubmission) {
      onSubmission();
    }
  };

  const updatePromptOrder = (newPromptId) => {
    const order = JSON.parse(localStorage.getItem("promptOrder")) || [];
    order.push(newPromptId);
    localStorage.setItem("promptOrder", JSON.stringify(order));
  };

  const handleKeyPress = (event) => {
    if (event.key === "Enter") {
      event.preventDefault();
      handleSubmit();
    }
  };

  return (
    <div className="PromptInput">
      <textarea
        ref={textareaRef}
        type="text"
        value={submittedValue}
        onChange={handleInputChange}
        onKeyPress={handleKeyPress}
        className="PromptInput"
        rows="2"
        placeholder="Type something then hit Enter to submit"
      />
    </div>
  );
};

export default PromptInput;
