import React from "react";

const ClearList = ({ onClear }) => {
  const handleClearClick = () => {
    // Clear all values in local storage
    Object.keys(localStorage).forEach((key) => {
      if (key.startsWith("promptInput-")) {
        localStorage.removeItem(key);
      }
    });

    if (onClear) {
      onClear();
    }
  };

  return (
    <div className="PromptsListActions">
      <button onClick={handleClearClick} className="clearPrompts">
        Clear Prompts
      </button>
    </div>
  );
};

export default ClearList;
