import React, { useState, useEffect } from "react";
import PromptInput from "./components/PromptInput";
import PromptsList from "./components/PromptsList";
import ClearList from "./components/ClearList";
import GenerateString from "./components/GenerateString";
import "./App.css";

const App = () => {
  const [prompts, setPrompts] = useState([]);
  const [refreshKey, setRefreshKey] = useState(0);
  const [generatedImageUrl, setGeneratedImageUrl] = useState("");

  const loadPrompts = () => {
    const order = JSON.parse(localStorage.getItem("promptOrder")) || [];
    const loadedPrompts = order
      .map((id) => {
        const promptData = JSON.parse(
          localStorage.getItem(`promptInput-${id}`)
        );
        return promptData
          ? {
              id: promptData.id,
              value: promptData.promptValue,
              creationDate: promptData.creationDate,
            }
          : null;
      })
      .filter((prompt) => prompt !== null);

    setPrompts(loadedPrompts);
  };

  useEffect(() => {
    loadPrompts();
  }, [refreshKey]);

  const handleSubmission = () => {
    setRefreshKey((prevKey) => prevKey + 1);
  };

  const handleClear = () => {
    setRefreshKey((prevKey) => prevKey + 1);
  };

  const handleSortEnd = (newPrompts) => {
    setPrompts(newPrompts);
    saveSortedPrompts(newPrompts);
  };

  const saveSortedPrompts = (sortedPrompts) => {
    const order = sortedPrompts.map((prompt) => prompt.id);
    localStorage.setItem("promptOrder", JSON.stringify(order));
  };

  const handleImageGenerated = (imageUrl) => {
    setGeneratedImageUrl(imageUrl);
  };

  return (
    <>
      <div className="container">
        <div className="row">
          <div className="col settings">
            <PromptInput onSubmission={handleSubmission} />
            <PromptsList
              prompts={prompts}
              onSortEnd={handleSortEnd}
              key={refreshKey}
            />
            <ClearList onClear={handleClear} />
            <GenerateString
              prompts={prompts}
              onImageGenerated={handleImageGenerated}
            />
          </div>
          <div className="col generations">
            {generatedImageUrl && (
              <img
                className="generated-img"
                src={generatedImageUrl}
                alt="Generated content"
              />
            )}
          </div>
        </div>
      </div>
    </>
  );
};

export default App;
