import React, { useState, useEffect } from "react";
import PromptItem from "./PromptItem";
import SmartPromptModal from "./SmartPromptModal";
import { SortableContainer, SortableElement } from "react-sortable-hoc";
import { arrayMoveImmutable as arrayMove } from "array-move";
import RemovePromptItem from "./RemovePromptItem";

const SortableItem = SortableElement(
  ({ value, onAddMoreClick, onEdit, onRightClick }) => (
    <PromptItem
      prompt={value}
      onAddMoreClick={onAddMoreClick}
      onEdit={onEdit}
      onRightClick={onRightClick}
    />
  )
);

const SortableList = SortableContainer(
  ({ items, onSortEnd, onAddMoreClick, onEdit, onRightClick }) => {
    return (
      <ul className="PromptList">
        {items.map((value, index) => (
          <SortableItem
            key={`item-${value.id}`}
            index={index}
            value={value}
            onAddMoreClick={onAddMoreClick}
            onEdit={onEdit}
            onRightClick={onRightClick}
          />
        ))}
      </ul>
    );
  }
);

const PromptsList = ({ prompts, onSortEnd }) => {
  const [selectedPrompt, setSelectedPrompt] = useState(null);
  const [contextMenuInfo, setContextMenuInfo] = useState({
    visible: false,
    x: 0,
    y: 0,
    promptId: null,
  });

  const handleRightClick = (event, promptId) => {
    event.preventDefault();
    setContextMenuInfo({
      visible: true,
      x: event.clientX,
      y: event.clientY,
      promptId,
    });
  };

  const handleRemoveClick = (promptId) => {
    setContextMenuInfo({ visible: false, x: 0, y: 0, promptId: null });
    const updatedPrompts = prompts.filter((prompt) => prompt.id !== promptId);
    onSortEnd(updatedPrompts);
    localStorage.removeItem(`promptInput-${promptId}`);
  };

  useEffect(() => {
    const closeContextMenu = () =>
      setContextMenuInfo({ visible: false, x: 0, y: 0, promptId: null });
    document.addEventListener("click", closeContextMenu);
    return () => document.removeEventListener("click", closeContextMenu);
  }, []);

  const handleSortEnd = ({ oldIndex, newIndex }) => {
    const newPrompts = arrayMove(prompts, oldIndex, newIndex);
    onSortEnd(newPrompts);
  };

  const handleAddMoreClick = (promptId) => {
    const prompt = prompts.find((p) => p.id === promptId);
    setSelectedPrompt(prompt);
  };

  const handleCloseModal = () => {
    setSelectedPrompt(null);
  };

  const handleEditPrompt = (promptId, newPromptValue) => {
    const updatedPrompts = prompts.map((prompt) =>
      prompt.id === promptId ? { ...prompt, value: newPromptValue } : prompt
    );

    onSortEnd(updatedPrompts);

    // Update local storage
    const promptData = JSON.parse(
      localStorage.getItem(`promptInput-${promptId}`)
    );
    if (promptData) {
      promptData.promptValue = newPromptValue;
      localStorage.setItem(
        `promptInput-${promptId}`,
        JSON.stringify(promptData)
      );
    }
  };

  return (
    <>
      <SortableList
        distance={5}
        items={prompts}
        onSortEnd={handleSortEnd}
        onAddMoreClick={handleAddMoreClick}
        onEdit={handleEditPrompt}
        onRightClick={handleRightClick}
      />
      {selectedPrompt && (
        <SmartPromptModal prompt={selectedPrompt} onClose={handleCloseModal} />
      )}
      {contextMenuInfo.visible && (
        <RemovePromptItem
          onRemove={() => handleRemoveClick(contextMenuInfo.promptId)}
          style={{ top: contextMenuInfo.y, left: contextMenuInfo.x }}
        />
      )}
    </>
  );
};

export default PromptsList;
