// GenerateString.js
import React, { useState } from "react";
import generateApiCall from "./GenerateApiCall";

const GenerateString = ({ prompts, onImageGenerated }) => {
  const [isGenerating, setIsGenerating] = useState(false);

  const generateString = () => {
    setIsGenerating(true);
    const promptValues = prompts.map((prompt) => prompt.value).join(", ");

    generateApiCall(promptValues, (imageUrl) => {
      onImageGenerated(imageUrl);
      setIsGenerating(false);
    });
  };

  return (
    <div className="generateBtn">
      <button onClick={generateString} disabled={isGenerating}>
        {isGenerating ? "Generating..." : "Generate"}
      </button>
    </div>
  );
};

export default GenerateString;
