// GenerateApiCall.js
import axios from "axios";

const generateApiCall = async (promptString, onImageGenerated) => {
  const apiUrl =
    "https://api.stability.ai/v1/generation/stable-diffusion-xl-1024-v1-0/text-to-image";
  const apiKey = process.env.REACT_APP_DREAM_API_KEY;

  if (!apiKey) {
    console.error("Missing Stability API key.");
    return;
  }

  try {
    const response = await axios.post(
      apiUrl,
      JSON.stringify({
        text_prompts: [{ text: promptString }],
        cfg_scale: 7,
        height: 1024,
        width: 1024,
        steps: 30,
        samples: 1,
      }),
      {
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
          Authorization: `Bearer ${apiKey}`,
        },
      }
    );

    // Assuming the response includes an array of artifacts,
    // and each artifact contains a base64-encoded image.
    if (response.data.artifacts && response.data.artifacts.length > 0) {
      const imageBase64 = response.data.artifacts[0].base64;
      const imageUrl = `data:image/png;base64,${imageBase64}`;
      onImageGenerated(imageUrl);
    }
  } catch (error) {
    console.error("Error calling the API:", error);
  }
};

export default generateApiCall;
