import React, { useState } from "react";
import LightingOptions from "./LightingOptions";

const SmartPromptModal = ({ onClose }) => {
  const [activeComponent, setActiveComponent] = useState("list");

  const handleItemClick = (componentName) => {
    setActiveComponent(componentName);
  };

  const goBackToList = () => {
    setActiveComponent("list");
  };

  const renderContent = () => {
    switch (activeComponent) {
      case "Lighting":
        return <LightingOptions onBack={goBackToList} />;
      // Add cases for other components if needed
      default:
        return (
          <ul className="photography-options">
            <li
              className="active-now"
              onClick={() => handleItemClick("Lighting")}
            >
              Lighting
            </li>
            <li>Styles</li>
            <li>Photographer</li>
            <li>Scene</li>
          </ul>
        );
    }
  };

  return (
    <div className="smartPromp__modal">
      <div className="modalContent">
        <div className="modal_header">
          <p>Browse more ideas for your prompt</p>
          <span className="modalCloseBtn" onClick={onClose}>
            X
          </span>
        </div>
        <div className="modal_body">{renderContent()}</div>
      </div>
    </div>
  );
};

export default SmartPromptModal;
