import React, { useState } from "react";

const AmbientLight = () => {
  const [selectedOption, setSelectedOption] = useState(null);

  const options = [
    {
      title: "Indoor",
      value: "Indoor lighting",
      imgSrc: require("../icons/indoor.svg").default,
    },
    {
      title: "Outdoor",
      value: "Outdoor lighting",
      imgSrc: require("../icons/outdoor.svg").default,
    },
  ];

  const handleSelect = (option) => {
    setSelectedOption(selectedOption === option.title ? null : option.title);
  };

  return (
    <div className="ambientLightSelect">
      {options.map((option, index) => (
        <div
          key={index}
          className={`option${
            selectedOption === option.title ? " selected_option" : ""
          }`}
          onClick={() => {
            handleSelect(option);
          }}
        >
          <img src={option.imgSrc} alt={option.title} />
          <span>{option.title}</span>
        </div>
      ))}
    </div>
  );
};

export default AmbientLight;
